<template>
  <div class="front">
    <h1>Hi </h1>

    <p class="mt-10 mb-0" style="color: #fff;">If you are an <span class="font-bold">existing user</span>, please type your client token down below.</p>
    <div class="front-card">
      <form @submit.prevent="onLogin">
        <div class="form-group" style="position: relative;">
          <label for="">Token <span class="text-danger">*</span></label>
          <input type="text" :class="{ 'has-error': 'token' in signinErrors }" class="form-control" style="padding-right: 80px;" v-model="token" />
          <span class="text-xs text-danger" v-if="'token' in signinErrors">{{ signinErrors.token }}</span>
          <button type="submit" class="btn btn-secondary" style="position: absolute; right: 5px; top: 28px; padding: 7px 9px;" :disabled="signinLoading">
            <span v-if="signinLoading">Loading...</span>
            <span v-else>Enter</span>
          </button>
        </div>
      </form>
    </div>

    <p class="mt-10 mb-0" style="color: #fff;">...or fill up the form if you are a <span class="font-bold">new user</span>.</p>
    <div class="front-card">
      <form @submit.prevent="onRegister">
        <div class="form-group"></div>
        <div class="form-group">
          <label for="">Full Name <span class="text-danger">*</span></label>
          <input type="text" :class="{ 'has-error': 'name' in signupErrors }" class="form-control" v-model="name" />
          <span class="text-xs text-danger" v-if="'name' in signupErrors">{{ signupErrors.name }}</span>
        </div>
        <div class="form-group">
          <label for="">Email <span class="text-danger">*</span></label>
          <input type="email" :class="{ 'has-error': 'email' in signupErrors }" class="form-control" v-model="email" />
          <span class="text-xs text-danger" v-if="'email' in signupErrors">{{ signupErrors.email }}</span>
        </div>
        <div class="form-group">
          <label for="">Phone</label>
          <input type="text" class="form-control" v-model="phone" />
        </div>
        <div class="form-group mb-0">
          <button type="submit" class="btn btn-secondary" :disabled="signupLoading">
            <span v-if="signupLoading">Loading...</span>
            <span v-else>Sign Up</span>
          </button>
          <!-- <button type="button" class="btn btn-secondary" @click="signupModal = true">
            Show Modal
          </button> -->
        </div>
      </form>
    </div>

    <modal v-show="signupModal" @close="(signupModal = false), ($store.state.widget.widget.operation = 'list')">
      <template slot="body">
        <InlineSvg :name="`undraw/mail_sent.svg`" size="10rem" />

        <div style="text-align: center;">
          <p class="mb-0">Please check your inbox.</p>
          <p class="my-0">We have sent you your client token.</p>
        </div>
      </template>
    </modal>

    <div class="front-powered">Powered by Xerodesk</div>
  </div>
</template>

<script>
import { IS_DEVELOPMENT } from "@Scripts/constants";

export default {
  name: "ChatFront",
  data: () => ({
    token: null,
    name: !IS_DEVELOPMENT ? null : "John Doe",
    email: !IS_DEVELOPMENT ? null : "john@doe.com",
    phone: null,

    signinErrors: {},
    signinLoading: false,

    signupErrors: {},
    signupLoading: false,
    signupModal: false
  }),
  methods: {
    async onLogin() {
      this.signinLoading = true;
      this.signinErrors = {};

      try {
        await this.$store.dispatch("client/fetch", this.token);
        this.$store.state.widget.widget.operation = "list";
      } catch (error) {
        this.signinErrors = {};

        if (error.response.status == 404) this.signinErrors.token = "The token field is invalid.";
      }

      this.signinLoading = false;
    },
    async onRegister() {
      this.signupLoading = true;
      this.signupErrors = {};

      try {
        await this.$store.dispatch("client/create", {
          name: this.name,
          email: this.email,
          phone: this.phone
        });
        this.signupModal = true;
      } catch (error) {
        this.signupErrors = {};

        const obj = error.response.data.errors;
        for (const key in obj) this.signupErrors[key] = obj[key][0];
      }

      this.signupLoading = false;
    }
  }
};
</script>
